import React from 'react';
//import {Link} from 'react-router';

const AboutMe = () => {
    return(
         <section className="sub about">
            <h2>
            A propos
            </h2>
            <p>
                {"Passionn d'informatique depuis ma plus tendre enfance, j'ai non seulement grandi avec les ordinateurs, mais aussi avec le WEB."}<br/><br/>
                {"C'est donc tout naturellement que je me suis tourn vers des tudes d'informatiques  l'EPITECH."}<br/><br/>
            </p>
            <p>
                {"Dveloppeur depuis 2007, j'ai touch  plusieurs technologies telles que PHP, JavaScript, ou la stack .Net."}<br/><br/>
                {"C'est dans cette dernire que je me suis spcialis, tout en gardant un sens critique exacerb vis--vis des technologies de Microsoft."}<br/><br/>
                {"Je ne regrette cependant pas mon choix au vu des dernires volutions de .Net Core"}<br/><br/>
            </p>
            <p>
                {"Mes maitres  penser dans l'industrie viennent de langages et d'horizons diffrents."}<br/><br/>
                {"Ils se nomment Martin Fowler, Greg Young, Jos Valim, Miran Lipovaa ou encore Uncle Bob pour ne citer qu'eux."}<br/><br/>
            </p>
        </section>
    );
}


export default AboutMe;


